/* General app styling */
.app-container {
    padding: 20px;
    font-family: Arial, sans-serif;
    transition: background-color 0.5s ease;
    min-height: 100vh;
  }
  
  /* Default background if no weather data is loaded */
  .default {
    background-color: #f0f0f0;
    color: #333;
  }
  
  /* Background styling for sunny weather */
  .sunny {
    background: linear-gradient(135deg, #ffe259, #ffa751);
    color: #333;
  }
  
  /* Background styling for rainy weather */
  .rainy {
    background: linear-gradient(135deg, #74ebd5, #acb6e5);
    color: #fff;
  }
  
  /* Background styling for cloudy weather */
  .cloudy {
    background: linear-gradient(135deg, #bdc3c7, #2c3e50);
    color: #fff;
  }
  
  /* Background styling for snowy weather */
  .snowy {
    background: linear-gradient(135deg, #e0eafc, #cfdef3);
    color: #333;
  }
  
  /* Background styling for thunderstorm conditions */
  .thunder {
    background: linear-gradient(135deg, #373b44, #4286f4);
    color: #fff;
  }
  
  /* Search box styling */
  .search-box {
    margin-bottom: 20px;
  }
  
  .search-box input {
    padding: 10px;
    font-size: 16px;
  }
  
  .search-box button {
    padding: 10px 20px;
    font-size: 16px;
    margin-left: 10px;
  }
  
  /* Weather information styling */
  .weather-info {
    background: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 8px;
    max-width: 400px;
  }
  